.title{
    margin-top: 10%;
    text-shadow: 2px 5px 6px rgba(128, 0, 0, 0.5);
    font-size: 80px;
    margin-left: 20px;
    margin-right: 20px;
}
@media only screen and (max-width: 1000px) {
    .title{
        margin-top: 25%;
    }
  }

a{
    text-decoration: none;
    color: white;
}

body {
    margin: 0;
    padding: 0;
    height: 800px;
}
 
.footer {
    background-color: #5F5F5F;
    border-top: 2px solid red;
    position: fixed;
    width: 100%;
    bottom: 0;
    color: white;
    font-size: 25px;
}

body{
    background-color: #ffffff;
}

.text-stroke{
    text-shadow: 3px 7px 8px rgba(0, 0, 0, 0.5);
    }

.description{
    font-size: 30px;
    margin-left: 20px;
    margin-right: 20px;
}

.btn{
    background-color: #0029FF;
    border: none;
    color: #ffffff;
    font-size: 20px;
    margin: 10px;
    border-radius: 15%;
    padding: 10px;
}